import axios from 'axios';

// const baseURL = process.env.REACT_APP_API_BASEURL;
const baseURL = process.env.NODE_ENV === 'development' ? process.env.REACT_APP_API_BASEURL : 'https://asking-board.fly.dev/api/v1';

//
//
export interface formData {
  email: string;
  role: string;
  password?: string;
  newPassword?: string;
  confirmPassword?: string;
}
export const accountSettings = async (token: string, formData: formData) => {
  try {
    const { status, data } = await axios.put(
      `${baseURL}/users/account`,
      formData,
      {
        headers: {
          'Content-Type': 'application/json',
          Authorization: 'Bearer ' + token,
        },
      },
    );
    if (status === 200) {
      return data;
    }
  } catch (error) {
    if (axios.isAxiosError(error)) {
      const errorMessageData = error.response?.data;
      return errorMessageData;
    }
    throw new Error('different error than axios');
  }
};

//
export interface ProfileFormData {
  avatar: string | File;
  name: string;
  introduction: string;
}
export const putUserProfile = async (
  token: string,
  { avatar, name, introduction }: ProfileFormData,
) => {
  try {
    const { data } = await axios.put(`${baseURL}/users`, {
      avatar,
      name,
      introduction,
    }, {
      headers: {
        'Content-Type': 'multipart/form-data',
        Authorization: 'Bearer ' + token,
      },
    });

    return data;
  } catch (error) {
    console.log(error);
  }
};
//EditModal
export const getUserProfile = async (token: string) => {
  try {
    const { data } = await axios.get(`${baseURL}/users`, {
      headers: {
        Authorization: 'Bearer ' + token,
      },
    });

    return data.currentUser;
  } catch (error) {
    console.log(error);
  }
}

