import axios from "axios";

// const baseURL = process.env.REACT_APP_API_BASEURL;
const baseURL = process.env.NODE_ENV === 'development' ? process.env.REACT_APP_API_BASEURL : 'https://asking-board.fly.dev/api/v1';

//API()
///
export interface loginParams {
  email: string;
  password: string;
}

export const loginRequest = async ({ email, password }: loginParams, role: string) => {
  try { 
    const { data } = await axios.post(`${baseURL}/${role}/login`, {
      email,
      password
    });
    const { token } = data;
    
    if(token) { 
      return data;
    }

  } catch (error) {
    console.log(error);
    if (axios.isAxiosError(error)) {
      const errorMessage = error.response?.data.message
      return errorMessage;
    }
    throw new Error('different error than axios');
  }
}

//
export interface registParams {
  name: string;
  email: string;
  password: string;
  confirmPassword: string;
  role: string;
}

export const registRequest = async ({
  name,
  email,
  password,
  confirmPassword,
  role
}: registParams) => {
  try { 
    const { status, data } = await axios.post(`${baseURL}/users`, {
      name,
      email,
      password,
      confirmPassword,
      role
    });
    if (status === 200) {
      return data;
    }

  } catch (error) {
    console.log(error);
    if (axios.isAxiosError(error)) {
      const errorMessage = error.response?.data.message
      return errorMessage;
    }
    throw new Error('different error than axios');
  }
}

//API
export const logout = () => {
  localStorage.removeItem('token');
  localStorage.removeItem('currentUser');
}


//google
//google
export const googleAuthRequest = async () => {
  try {
    const {status, data} = await axios.get(`${baseURL}/auth/google/callback`, {
      timeout: 10000,
    });

    if (status === 200) {
      console.log(data);
      return data;
    }
  } catch (error) {
    console.log(error);
    if (axios.isAxiosError(error)) {
      const errorMessage = error.response?.data.message
      return errorMessage;
    }
    throw new Error('different error than axios');
  }
}

export const aTagUrlForGoogle = `${baseURL}/auth/google`;